body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Formular", sans-serif;
  background: linear-gradient(180deg, #004fd6 25%, #002970 100%);
  color: #ffffff;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.4;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

.bottom-overflow {
  overflow: hidden;
}

main {
  position: relative;
  z-index: 1;
}

a {
  color: #ffffff;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

[hidden] {
  display: none !important;
}

/* Mobile-menu */
[data-state=mobile-menu] .header__menu-wrapper {
  transform: translateX(0);
}
[data-state=mobile-menu] .overlay {
  opacity: 1;
  pointer-events: auto;
}

/* Adv mark article */
.token-mark-block {
  display: none;
  position: relative;
  z-index: 20;
  max-width: 1288px;
  width: 100%;
  padding: 0 20px;
  margin: 30px auto 0;
  box-sizing: border-box;
}

.article-mark:hover + .token-info {
  opacity: 1;
  pointer-events: auto;
}

.token-info {
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 270px;
  border: 2px solid #ff9c34;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(11px);
  border-radius: 2px;
  padding: 20px 10px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

.token-info-item {
  display: block;
  color: #ffffff;
  font-size: 12px;
  line-height: 1.2;
}
.token-info-item a {
  color: #ffffff;
  text-decoration: underline;
}
.token-info-item a:hover {
  text-decoration: none;
}

/*    */
.overlay {
  position: fixed;
  inset: 0;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
}

/* Swiper */
.swiper-nav {
  position: absolute;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  pointer-events: none;
}
@media only screen and (max-width: 991px) {
  .swiper-nav {
    justify-content: center;
    gap: 20px;
  }
}
.swiper-nav .swiper-btn-prev,
.swiper-nav .swiper-btn-next {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 37px;
  background-color: #ff9c34;
  border-radius: 10px 6px 10px 6px;
  transform: skewX(-8deg);
  cursor: pointer;
  pointer-events: auto;
  transition: background-color 0.3s ease;
}
.swiper-nav .swiper-btn-prev:hover,
.swiper-nav .swiper-btn-next:hover {
  background-color: rgba(255, 156, 52, 0.7);
}
.swiper-nav .swiper-btn-prev svg,
.swiper-nav .swiper-btn-next svg {
  transform: skewX(8deg);
}
.swiper-nav .swiper-button-disabled {
  filter: grayscale(100%);
  cursor: default;
}

/* Swiper pagination */
.swiper-pagination {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: unset !important;
  bottom: 16px !important;
  z-index: 12;
}
@media only screen and (max-width: 991px) {
  .swiper-pagination {
    display: flex;
  }
}
.swiper-pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  opacity: 0.5;
  background-color: #ffffff;
  margin: 0 !important;
}
.swiper-pagination .swiper-pagination-bullet-active {
  opacity: 1;
}

/* Blocks */
